@import "https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap";
body {
  background-color: #f6f0db;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  transition: background-color .7s, color .7s;
}

.nav-bar {
  justify-content: space-between;
  padding: 12px 20px;
  display: flex;
}

.nav-left {
  gap: 10px;
  display: flex;
}

.nav-right {
  justify-items: right;
}

#theme-toggle {
  border-radius: 100px;
}

.wordle-button, .theme-button {
  color: #fff;
  cursor: pointer;
  background-color: #6aaa64;
  border: none;
  border-radius: 6px;
  padding: 10px 18px;
  font-size: 1rem;
  font-weight: bold;
  transition: all .5s;
  box-shadow: 0 2px #4b7c4a;
}

.wordle-button:hover, .theme-button:hover {
  background-color: #4a7e46;
}

.wordle-button:active, .theme-button:active {
  transform: translateY(5px);
}

body.dark-theme {
  color: #000;
  background-color: #252530;
}

body.dark-theme .wordle-button, body.dark-theme .theme-button {
  color: #fff;
  background-color: #565758;
  box-shadow: 0 2px #333;
}

body.dark-theme .title {
  color: #fff;
}

.main-header {
  text-align: center;
}

.title {
  color: #333;
  letter-spacing: 2px;
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: Rubik Mono One, sans-serif;
  font-size: 3rem;
}

#board {
  perspective: 500px;
  place-content: start center;
  gap: 10px;
  max-width: fit-content;
  margin: 0 auto;
  padding: 20px;
  display: grid;
}

.tile {
  text-transform: uppercase;
  user-select: none;
  width: 47px;
  height: 47px;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  will-change: transform;
  background: #fff;
  border: 2px solid #d3d6da;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  transition: transform .4s;
  display: flex;
  position: relative;
  box-shadow: 0 2px #999;
}

.tile:hover {
  transform: translateY(2px);
  box-shadow: 0 4px #999;
}

.keyboard {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  margin-top: 20px;
  display: flex;
}

.keyboard-row {
  gap: 6px;
  display: flex;
}

.key {
  cursor: pointer;
  background-color: #d3d6da;
  border: none;
  border-radius: 6px;
  padding: 12px 16px;
  font-size: 1rem;
  font-weight: bold;
  transition: all .2s;
  box-shadow: 0 2px #999;
}

.key.absent {
  color: #fff;
  background-color: #787c7e;
  box-shadow: 0 2px #888;
}

.key.correct {
  color: #fff;
  background-color: #6aaa64;
}

.key.present {
  color: #fff;
  background-color: #c9b458;
}

.key:active {
  box-shadow: none;
  color: #fff;
  transform: translateY(5px);
}

.key:hover {
  color: #fff;
  background-color: #888;
}

.nav-bar {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.wordle-button {
  color: #fff;
  cursor: pointer;
  background-color: #6aaa64;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: transform .1s, background-color .3s;
  box-shadow: 0 4px #4b7c4a;
}

.wordle-button:hover {
  background-color: #5a9a55;
}

.wordle-button:active {
  transform: translateY(2px);
  box-shadow: 0 2px #4b7c4a;
}

.mode-select {
  justify-content: center;
  gap: 12px;
  display: flex;
}

.hidden {
  display: none;
}

.correct {
  color: #fff;
  background-color: #6aaa64;
}

.present {
  color: #fff;
  background-color: #c9b458;
}

.absent {
  color: #fff;
  background-color: #787c7e;
}

.message {
  color: #fff;
  z-index: 9999;
  opacity: 0;
  pointer-events: none;
  background-color: gray;
  border-radius: 8px;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  display: none;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.message.show {
  opacity: 1;
  top: 60px;
}

.message.dark-theme {
  color: #000;
  background-color: #ddd;
}

.message.neon-red-box {
  color: #fff;
  background-color: #000;
  border: 2px solid #ff073a;
  border-radius: 8px;
  padding: 1rem;
  font-weight: bold;
  box-shadow: 0 0 5px #ff073a, 0 0 10px #ff073a, 0 0 20px #ff073a, 0 0 40px #ff073a;
}

.neon-green-box {
  color: #fff;
  background-color: #000;
  border: 2px solid #3f0;
  border-radius: 8px;
  padding: 1rem;
  font-weight: bold;
  box-shadow: 0 0 5px #3f0, 0 0 10px #3f0, 0 0 20px #3f0, 0 0 40px #3f0;
}

.neon-yellow-box {
  color: #fff;
  background-color: #000;
  border: 2px solid #fe0;
  border-radius: 8px;
  padding: 1rem;
  font-weight: bold;
  box-shadow: 0 0 5px #fe0, 0 0 10px #fe0, 0 0 20px #fe0, 0 0 40px #fe0;
}

.graffiti {
  background: hsl(calc(360 * var(--i, 0)), 100%, 50%);
  pointer-events: none;
  z-index: 9999;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  position: absolute;
}

@media (width <= 768px) {
  .title {
    font-size: 2rem;
  }

  .wordle-button, .theme-button {
    padding: 8px 14px;
    font-size: .9rem;
  }

  .keyboard-row {
    flex-wrap: wrap;
    justify-content: center;
    gap: 4px;
  }

  .key {
    min-width: 8vw;
    padding: 10px 12px;
    font-size: .9rem;
  }

  .tile {
    border-radius: 5px;
    width: 12vw;
    height: 12vw;
    font-size: 1.2rem;
  }

  #board {
    gap: 6px;
    padding: 10px;
  }

  .message {
    padding: .6rem 1rem;
    font-size: 1rem;
    top: 10px;
  }

  .neon-red-box, .neon-yellow-box, .neon-green-box {
    padding: .8rem;
    font-size: .9rem;
  }
}

@media (width <= 480px) {
  .tile {
    width: 14vw;
    height: 14vw;
    font-size: 1rem;
  }

  .keyboard-row {
    gap: 2px;
  }

  .key {
    padding: 8px;
    font-size: .8rem;
  }

  .wordle-button, .theme-button {
    padding: 6px 10px;
    font-size: .85rem;
  }
}

body {
  overflow-x: hidden;
}
/*# sourceMappingURL=wordle.5e48fb20.css.map */
